import { Component, OnInit } from '@angular/core';
import { Colis } from 'src/app/models/colis';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { ColisService } from 'src/app/services/colis.service';
@Component({
  selector: 'app-allcolissuper',
  templateUrl: './allcolissuper.component.html',
  styleUrls: ['./allcolissuper.component.css']
})
export class AllcolissuperComponent implements OnInit {
coliers: Colis[];
p:number;
  constructor(private colisService:ColisService) {
    this.coliers= [];
   }
  config:any;
  ngOnInit(): void {
    this.get();
  }
  get(){
    this.colisService.super().subscribe(data => {
      this.coliers = data;})
      this.config = {
        itemsPerPage: 5,
        currentPage: 1,
        totalItems: this.coliers.length
      };
   }
   pageChanged(event){
    this.config.currentPage = event;
  }
}
