import { Component, OnInit } from '@angular/core';
import { Codecoli } from 'src/app/models/codecoli';
import { Etatcolis } from 'src/app/models/etatcolis';
import { ColisService } from 'src/app/services/colis.service';

@Component({
  selector: 'app-suivrecolisall',
  templateUrl: './suivrecolisall.component.html',
  styleUrls: ['./suivrecolisall.component.css']
})
export class SuivrecolisallComponent implements OnInit {

  code:Codecoli;
  tablec:Etatcolis[];
  test:boolean;
    constructor(private colisservice:ColisService) {
      this.code=new Codecoli;
      this.test=false;
     }
  
    ngOnInit(): void {
    }
    onChercher(f:any){
      console.log(f.keyword);
      this.test=true;
      this.colisservice.Suivrecolis(f.keyword).subscribe(data =>{this.tablec=data;} );
    }
  
  }
  