import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { AuthLayoutComponent } from './layouts/auth-layout/auth-layout.component';
import { LoginComponent } from "./authentification/login/login.component";
import {  AuthentificationModule} from "./authentification/authentification.module";
import { AuthGuard } from "./guards/auth.guard";

import { UserlayoutComponent } from './user/userlayout/userlayout.component';
import { SuperadminlayoutComponent } from './superadmin/superadminlayout/superadminlayout.component';




const routes: Routes =[

  
  {
    path: '',
    redirectTo: 'llogin',
    pathMatch: 'full',
  },
  
  
  {
    path: 'llogin',
    component:LoginComponent ,
    children: [
      {
        path: 'auth',
        loadChildren: './authentification/authentification.module#AuthentificationModule'
      }
    ],
    
  },
  {
    path: '',
    component:UserlayoutComponent,

     canActivate:[AuthGuard],
    children: [
      {
        path: '',
        loadChildren: () => import('./user/user.module').then(m => m.UserModule)
      }
    ]
  },
  {
    path: '',
    component:SuperadminlayoutComponent,

    canActivate:[AuthGuard],
    children: [
      {
        path: '',
        loadChildren: () => import('./superadmin/superadmin.module').then(m => m.SuperadminModule)
        //loadChildren: './superadmin/superadmin.module#SuperadminModule'
      }
    ]
  },



  {
    path: '',
    component: AdminLayoutComponent,

   canActivate:[AuthGuard],

    children: [
      {
        path: '',
        loadChildren: () => import('./layouts/admin-layout/admin-layout.module').then(m => m.AdminLayoutModule)
      }
    ],
    
  }, {
    path: 'lay',
    component: AuthLayoutComponent,
    
    children: [
      {
        path: 'hj',
        loadChildren: './layouts/auth-layout/auth-layout.module#AuthLayoutModule'
      },


     
    ]
  }, 


];


@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes,{
      useHash: true,onSameUrlNavigation:'reload'
    },)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
