import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';
import { Reclamation } from '../models/reclamation';

@Injectable({
  providedIn: 'root'
})
export class ServicereclamationService {

  private  reclamationUrl:string;
  constructor(private http: HttpClient) {
   // environment.BACKEND_URL = 'http://5.196.7.152:8080/org-sid/reclamation';
   // environment.BACKEND_URL = /reclamation'http://localhost:8080/reclamation';
   }
   
 
  public fin(): Observable<Reclamation[]> {
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
      "Authorization": localStorage.getItem('jwt')!== null ?  "Bearer " +  localStorage.getItem('jwt'): ''
    
    }
      
      
      
      );
  let options = { headers: headers };

    return this.http.get<Reclamation[]>(environment.BACKEND_URL + "/reclamation/all",options);
   }
  
  public Save(recl: Reclamation) {
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
      "Authorization": localStorage.getItem('jwt')!== null ?  "Bearer " +  localStorage.getItem('jwt'): ''
    
    }
      
      
      
      );
  let options = { headers: headers };

    return this.http.post<Reclamation>(environment.BACKEND_URL + '/reclamation/save',recl,options);
  }
}
