import { HttpClient, HttpErrorResponse, HttpHeaders,HttpRequest } from '@angular/common/http';
import { Injectable, Output, EventEmitter } from '@angular/core';
import { LocalStorageService } from 'ngx-webstorage';

import { BehaviorSubject, Observable, throwError } from 'rxjs';
import {  JwtHelperService} from "@auth0/angular-jwt";

import { map, tap } from 'rxjs/operators';
import { Emailoublie } from '../models/emailoublie';
import { LoginRequestPayload } from '../models/login-request-payload';
import { LoginResponse } from '../models/login-response';
import { Signuprequestpayload } from '../models/signuprequestpayload';
import { Changepassword } from '../models/changepassword';
import { environment}   from  "../../environments/environment.prod"
//import { Emailoublie } from './emailoublie';



@Injectable({
  providedIn: 'root'
})
export class AuthServiceService {
 public  orhgUrl:string;
  
  @Output() loggedIn: EventEmitter<boolean> = new EventEmitter();
  @Output() username: EventEmitter<string> = new EventEmitter();
  
  refreshTokenPayload = {
    refreshToken: this.getRefreshToken(),
    username: this.getUserName()
  }


  constructor(private httpClient : HttpClient,private localStorage: LocalStorageService) { 
   this.orhgUrl='http://localhost:8080/';
    //this.orhgUrl='http://5.196.7.152:8080/org-sid/';
  }
  jwtHelper:JwtHelperService = new JwtHelperService();
  loginSuject = new BehaviorSubject<any>(null);  
  resetpass(emailoublie:Emailoublie){
    let headers = new HttpHeaders({
      'Content-Type': 'application/json'});
  let options = { headers: headers };

    return this.httpClient.post<Emailoublie>(environment.BACKEND_URL + "/api/reset",JSON.stringify(emailoublie),options);
  }

  login(loginRequestPayload: LoginRequestPayload): Observable<boolean> {
    sessionStorage.setItem('id','1');
  //console.log(this.getUserName);
  let roles: string[] = [];
    return this.httpClient.post<LoginResponse>(environment.BACKEND_URL + '/api/signin',
      loginRequestPayload).pipe(map(data => {
        //console.log(data.username);
        this.localStorage.store('authenticationToken', data.authenticationToken);
        this.localStorage.store('username', data.username);
        this.localStorage.store('refreshToken', data.refreshToken);
        this.localStorage.store('expiresAt', data.expiresAt);
        localStorage.setItem('user', data.username);
       // console.log(this.localStorage.retrieve('authenticationToken'));
        localStorage.setItem("j","Bearer "+data.authenticationToken);
        localStorage.setItem("jwt",data.authenticationToken);
       
       // console.log(data.refreshToken);

      //  if (localStorage.getItem('jwt')) {
        // roles = this.jwtHelper.decodeToken(data.authenticationToken).roles;
          //alert(roles);
         // console.log(roles);
        //  console.log(localStorage.getItem('jwt'));
         // return roles.includes('ROLE_ADMIN');
      //  }


        


        return true;
      }));
  }

  isUserAdmin() {
    let roles: string[] = [];
    if (localStorage.getItem('jwt')) {
      roles = this.jwtHelper.decodeToken(localStorage.getItem('jwt')).roles;
    
     
      return roles;
    }
    return [];

  }

  getJwtToken(){
    return this.localStorage.retrieve('authenticationToken');}
    refreshToken() {
      return this.httpClient.post<LoginResponse>(environment.BACKEND_URL + '/api/refresh/token',
        this.refreshTokenPayload)
        .pipe(tap(response => {
          this.localStorage.clear('authenticationToken');
          this.localStorage.clear('expiresAt');
  
          this.localStorage.store('authenticationToken',
            response.authenticationToken);
          this.localStorage.store('expiresAt', response.expiresAt);
          this.localStorage.clear('user');
          // console.log(this.localStorage.retrieve('authenticationToken'));
           this.localStorage.clear("j");
           this.localStorage.clear("jwt");
        }));
    }
    
  logout() {

    this.httpClient.post(environment.BACKEND_URL + '/api/logout', this.refreshTokenPayload,
      { responseType: 'text' })
      .subscribe(data => {
       // console.log(data);
      }, error => {
        throwError(error);
       // console.log("error logout");
      })
    this.localStorage.clear('authenticationToken');
    this.localStorage.clear('username');
    this.localStorage.clear('refreshToken');
    this.localStorage.clear('expiresAt');
    this.localStorage.clear('user');
    // console.log(this.localStorage.retrieve('authenticationToken'));
     this.localStorage.clear("j");
     this.localStorage.clear("jwt");
  }

  getUserName() {
    return this.localStorage.retrieve('username');
  }
  getRefreshToken() {
    return this.localStorage.retrieve('refreshToken');
  }

  isLoggedIn(): boolean {
  
    return this.getJwtToken() != null;
  }



  isLoggedn(): boolean {
    this.logout();
    return true;
  }


  
 changepass(change:Changepassword){
   let headers = new HttpHeaders({
    'Content-Type': 'application/json'});
let options = { headers: headers }; 

  return this.httpClient.post<Changepassword>(environment.BACKEND_URL + "/api/change",change,options);
}
signup(signupRequestPayload:Signuprequestpayload){
  return this.httpClient.post(environment.BACKEND_URL + "/api/sign",signupRequestPayload,{responseType:'text'});
}
}
