import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';
import { Editprofil } from '../models/editprofil';
import { Information } from '../models/information';
import { Modifierpass } from '../models/modifierpass';

@Injectable({
  providedIn: 'root'
})
export class EditprofilService {
     orgUrl:string;
  constructor(private http:HttpClient) {
  
   }
  editProfile(p:Editprofil){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': localStorage.getItem('jwt') !== null ? 'Bearer ' + localStorage.getItem('jwt') : ''
      })
    };
    return this.http.post(environment.BACKEND_URL + "/user/editprofil",p,httpOptions);

  }
  info():Observable<Information>{
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': localStorage.getItem('jwt') !== null ? 'Bearer ' + localStorage.getItem('jwt') : ''
      })
    };
    return this.http.get<Information>(environment.BACKEND_URL + "/user/user",httpOptions);

  }
  editpass(mod:Modifierpass):Observable<Modifierpass>{
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': localStorage.getItem('jwt') !== null ? 'Bearer ' + localStorage.getItem('jwt') : ''
      })
    };
    return this.http.post<Modifierpass>(environment.BACKEND_URL + "/user/passwordchange",mod,httpOptions);}
  
    
}
