import { Component, OnInit, ElementRef } from '@angular/core';
import { ROUTES } from '../sidebar/sidebar.component';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { Router } from '@angular/router';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { Reclamation } from 'src/app/models/reclamation';
import { ServicereclamationService } from 'src/app/services/servicereclamation.service';
import { AuthServiceService } from 'src/app/services/auth.service';
import { EditprofilService } from 'src/app/services/editprofil.service';
import { Information } from 'src/app/models/information';
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  closeResult='';
  public focus;
  public listTitles: any[];
  public location: Location;
  reclamations:Reclamation[];
  x:string;
  private information:Information

  constructor(private profileSer :EditprofilService,private authservice:AuthServiceService ,private reclamationservice: ServicereclamationService,private modalService: NgbModal,location: Location,  private element: ElementRef, private router: Router) {
    this.location = location;
    this.information=new Information(); 

  }

  ngOnInit() {
    this.profileSer.info().subscribe(data=>{this.information=data;this.x=data.username;
    //  console.log(data);
    });
 
    this.listTitles = ROUTES.filter(listTitle => listTitle);
    this.reclamationservice.fin().subscribe(data => {
      this.reclamations = data;
      //console.log(data);
    });
  }
  getTitle(){
    var titlee = this.location.prepareExternalUrl(this.location.path());
    if(titlee.charAt(0) === '#'){
        titlee = titlee.slice( 1 );
    }

    for(var item = 0; item < this.listTitles.length; item++){
        if(this.listTitles[item].path === titlee){
            return this.listTitles[item].title;
        }
    }
    return 'Dashboard';
  }



open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

submit(){
  this.authservice.logout();}
}
