import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root'
})
export class SuperadminService {
orgUrl:string;
  constructor(private http:HttpClient) { 
  //environment.BACKEND_URL = "http://5.196.7.152:8080/org-sid/";
 // environment.BACKEND_URL = /"http://localhost:8080/";
  }




  public alladmin(): Observable<User[]> {
    
    return this.http.get<User[]>(environment.BACKEND_URL + "/user/adminn");
   }

   public active(user:User) {
     return this.http.post<User>(environment.BACKEND_URL + "/user/active",user);
   }
   
   public cherche(nom:any):Observable<User[]>{
    return this.http.get<User[]>(environment.BACKEND_URL + "/user/chercher/"+nom);}
}
